<template>
  <div class="row g-0">
      <div class="col-12 col-sm-8">
          <div class="position-relative frame" ref="frame">
              <img class="frame-img" ref="selectedFrameImg" :src="currentImage" />
              <video class="video" ref="video" width="500" height="640" autoplay playsinline></video>
              <canvas class="canvas" style="display: none" ref="canvas"></canvas>
              <img class="frame-img" v-if="capturedImageData" :src="capturedImageData" style="display: none" />
          </div>
  
          <div class="row g-0">
              <div class="col-12">
                  <button class="btn btn-primary mt-2 me-md-2" @click="captureImage">Capture Image</button>
                  <button class="btn btn-primary mt-2 me-md-2" @click="shareContent">Share Image</button>
                  <a href="https://streams-avatar.s3.ap-south-1.amazonaws.com/sample+video.mov" download="sample-video.mov" class="btn btn-primary mt-2 me-md-2" @click="shareContent">Download Video</a>
                  <p>{{outputMesssage}}</p>
              </div>
          </div>
      </div>
      <div class="col-12 col-sm-4">
          <div class="row">
              <div class="col-12 d-sm-flex align-items-top flex-column">
                  <img class="frame-btn mb-2 mt-2" id="image1" name="image1" src="@/assets/images/frame1.png" @click="showFrame1()" />
                  <img class="frame-btn" src="@/assets/images/frame2.png" @click="showFrame2()" />
              </div>
          </div>
      </div>
  </div>
  <br />
  <!-- <div class="frame position-relative">
              <canvas class="canvas" ref="canvas" style="display: none"></canvas>
              <img class="frame-img" v-if="capturedImageData" :src="capturedImageData" style="display: none" />
            </div> -->
  </template>
  
      
  <script>
  export default {
      name: "FrameComponent",
      data() {
          return {
              capturedImageData: null,
              image1: require("@/assets/images/frame1.png"),
              image2: require("@/assets/images/frame2.png"),
              currentImage: require("@/assets/images/frame1.png"),
  
              outputMesssage: '',
          };
      },
      mounted() {
          this.accessCamera();
      },
      methods: {
          accessCamera() {
              const video = this.$refs.video;
              const canvas = this.$refs.canvas;
              const imgFrame = this.$refs.selectedFrameImg;
              const ctx = canvas.getContext("2d");
  
              navigator.mediaDevices
                  .getUserMedia({
                      video: true
                  })
                  .then((stream) => {
                      video.srcObject = stream;
                      canvas.width = imgFrame.clientWidth;
                      canvas.height = imgFrame.clientHeight;
  
                      const drawFrame = () => {
                          ctx.drawImage(video, 0, 0, imgFrame.clientWidth, imgFrame.clientHeight);
                      };
                      setInterval(drawFrame, 100);
                  })
                  .catch((err) => {
                      console.error("Error accessing camera:", err);
                  });
          },
          captureImage() {
              const canvas = this.$refs.canvas;
              const ctx = canvas.getContext("2d");
              const video = this.$refs.video;
              const imgFrame = this.$refs.selectedFrameImg;
  
              const drawFrame = () => {
                  ctx.drawImage(video, 0, 0, imgFrame.clientWidth, imgFrame.clientHeight);
              };
              setInterval(drawFrame, 100);
  
              const frameImg = new Image();
              frameImg.onload = () => {
                  ctx.drawImage(frameImg, 0, 0, imgFrame.clientWidth, imgFrame.clientHeight);
                  this.capturedImageData = canvas.toDataURL();
  
                  const link = document.createElement("a");
                  link.download = "absolut.png";
                  link.href = canvas.toDataURL();
                  link.click();
              };
              frameImg.onerror = (error) => {
                  console.error("Error loading frame image:", error);
              };
              frameImg.src = this.currentImage;
          },
          showFrame1() {
              this.currentImage = this.image1;
          },
          showFrame2() {
              this.currentImage = this.image2;
          },
          async shareContent() {
              if (!this.capturedImageData) {
                  this.outputMessage = "No image to share.";
                  return;
              }
  
              if (!navigator.canShare) {
                  this.outputMessage = "Your browser doesn't support the Web Share API.";
                  return;
              }
  
              const blob = await (await fetch(this.capturedImageData)).blob();
              const file = new File([blob], "captured-image.png", {
                  type: blob.type
              });
  
              if (navigator.canShare({
                      files: [file]
                  })) {
                  try {
                      await navigator.share({
                          files: [file],
                          title: "Captured Image",
                          text: "Check out this image!",
                      });
                      this.outputMessage = "Shared!";
                  } catch (error) {
                      this.outputMessage = `Error: ${error.message}`;
                  }
              } else {
                  this.outputMessage = "Your system doesn't support sharing this image.";
              }
          },
      },
  
  };
  </script>
  
      
  <style scoped>
  /* Add your CSS styles here */
  .frame-img {
      position: relative;
      z-index: 1;
  
  }
  
  .frame {
      transform: translate3d(0, 0, 0);
      -webkit-transform: translate3d(0, 0, 0);
      overflow: hidden;
      margin: 0 auto;
      max-width: 100%;
      width: 100%;
  }
  
  .video,
  .canvas {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: fill;
      right: 0;
      margin: auto;
  }
  
  .canvas {
      display: none;
  }
  
  @media (min-width: 768px) {
      .frame {
          max-width: 350px;
      }
  }
  </style>